@font-face {
    font-family: 'Poiret One';
    src: url('/fonts/PoiretOne/PoiretOne-Regular.woff2') format('woff2'),
        url('/fonts/PoiretOne/PoiretOne-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}


/* raleway-200 - latin */
@font-face {
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 200;
  src: url('/fonts/Raleway/raleway-v11-latin-200.eot'); /* IE9 Compat Modes */
  src: local('Raleway ExtraLight'), local('Raleway-ExtraLight'),
       url('/fonts/Raleway/raleway-v11-latin-200.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('/fonts/Raleway/raleway-v11-latin-200.woff2') format('woff2'), /* Super Modern Browsers */
       url('/fonts/Raleway/raleway-v11-latin-200.woff') format('woff'), /* Modern Browsers */
       url('/fonts/Raleway/raleway-v11-latin-200.ttf') format('truetype'), /* Safari, Android, iOS */
       url('/fonts/Raleway/raleway-v11-latin-200.svg#Raleway') format('svg'); /* Legacy iOS */
}
